
html {
  height: 100%;
}
body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  background-color: #fafafa;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app {
  display: inline-block;
  width: 800px;
  height: 600px;
  border-radius: 10px;
  background-color: #444;
  overflow: hidden;
}
#information {
  float: left;
  width: 400px;
}
#information:after {
  content: '';
  display: block;
  clear: both;
}
#current-weather {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 200px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#forecast {
  height: 400px;
}
#map {
  float: right;
  width: 400px;
  height: 600px;
}
hr {
  width: 90%;
}



